<template>
  <div class="manage-add-wrapper">

    <el-form :model="userForm" :rules="rules" ref="userForm" label-width="100px" class="manage-add-form">

      <div class="base-set">
        <div class="line-title"></div>
        <el-form-item label="" prop="account" :style="{display:'inline-block'}">
          <el-input v-model="userForm.account"></el-input>
        </el-form-item>

        <el-form-item label="" prop="name" :style="{display:'inline-block'}">
          <el-input v-model="userForm.name"></el-input>
        </el-form-item>

        <el-form-item label="" prop="password" :style="{display:'inline-block'}">
          <el-input type="password" v-model="userForm.password" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="checkPass" :style="{display:'inline-block'}">
          <el-input type="password" v-model="userForm.checkPass" auto-complete="off"></el-input>
        </el-form-item>
      </div>

      <div class="line-title"></div>

      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
      <el-checkbox-group class="r-radio-box" v-model="checkedRight" @change="handleCheckedCitiesChange">
        <el-checkbox v-for="item in rightName" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>

      <el-form-item>
        <el-button type="primary" @click="submitForm('userForm')"></el-button>
        <el-button @click="resetForm('userForm')"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  import _ from 'lodash'

  export default {
    created(){
      // 
      this.$api.api_req('museum-api/user/auth-menu', 'GET', {}, this.initAuthLists, this.failure)
    },
    data(){
      // 
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else {
          if (this.userForm.checkPass !== '') {
            this.$refs.userForm.validateField('checkPass')
          }
          callback()
        }
      }
      // 
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.userForm.password) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }
      return {
        checkAll: false,
        rightName: [],            //  
        rights: [],               //  
        rightIndex: [],           //  
        checkedRight: [],         //  
        isIndeterminate: false,   // flag 
        userForm: {
          account: '',
          name: '',
          password: '',
          checkPass: ''
        },
        rules: {
          account: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 30, message: ' 1  30 ', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 30, message: ' 1  30 ', trigger: 'blur'}
          ],
          password: [
            {required: true, validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {required: true, validator: validatePass2, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      // 
      initAuthLists(data){
        data.data.menus.map((item, index) => {
          item.functions.map((item, index) => {
            this.rightName.push(item.functionName)      //  
            this.rights.push(item)                      //  
          })
        })
      },
      // 
      handleCheckAllChange(val) {
        this.checkedRight = val ? this.rightName : []   // true : false
        this.isIndeterminate = false
      },
      // 
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length
        this.checkAll = checkedCount === this.rightName.length
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.rightName.length
      },
      // 
      submitForm(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$api.api_req('museum-api/user/register', 'POST', _.pick(this.userForm, ['account', 'name', 'password']), this.submitFormSuc, this.failure)
          } else {
            this.$message.error({message: ''})
            return false
          }
        })
      },
      // , 
      submitFormSuc(data){
        //  
        this.rightIndex = []
        this.checkedRight.map((item, index) => {
          this.rightIndex.push(parseInt(_.find(this.rights, ['functionName', item]).id))
        })
        console.log(Array.from(this.rightIndex))
        this.$api.api_req('museum-api/user/user-auth', 'POST', {userId: data.data.id, authList: this.rightIndex}, this.registSuc, this.failure)
      },
      registSuc(data){
        console.log(data)
        this.$message({type: 'success', message: ''})
        this.$router.push({path: '/edit/manage/board'})
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.$message({message: ''})
      },
      failure(_err){
        console.log('')
        console.log(_err)
      }
    }
  }
</script>

<style lang="stylus">
  .manage-add-wrapper
    width: 600px
  //
    .line-title
      color: #1f2f3d;
      font-size: 22px;
      font-weight: 400;
      border-left 5px solid #409eff
      margin: 20px 0 20px 0
      padding-left: 10px
    .manage-add-form
      .base-set
        width: 400px
      .manage-right-box
        margin-left 50px
        margin-bottom: 20px
        .el-checkbox-group
          padding: 10px 0
      .el-input__inner
        width: 300px
    .el-form-item
      display: flex
      justify-content: center
    .el-checkbox
      width: 150px
      margin-left: 30px
      padding-bottom: 5px

  .el-checkbox
    margin 0
  .el-checkbox + .el-checkbox
    margin 0
</style>



