<template>
  <div class="add-exhibit-wrapper">

    <el-form :model="ruleForm" :rules="r_rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <div class="line-title"></div>
      <el-form-item label="" :style="{display:'inline-block'}" prop="relicType">
        <el-select v-model="ruleForm.relicType" placeholder="">
          <template v-for="(item,index) in relicType">
            <el-option :label=item.relicType :value=item.id.toString() :key="item.id"></el-option>
          </template>
        </el-select>
      </el-form-item>
      <el-form-item label="" :style="{display:'inline-block'}">
        <el-input v-model="ruleForm.id" :style="{width:'217px'}" readonly></el-input>
      </el-form-item>

      <div class="line-title">()</div>

      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name" placeholder=""></el-input>
      </el-form-item>

      <!-- -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}" prop="level">
          <el-select v-model="ruleForm.level" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :style="{display:'inline-block'}" prop="attritionRate">
          <el-select v-model="ruleForm.attritionRate" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </div>

      <!-- -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}" prop="amount">
          <el-input v-model="ruleForm.amount" placeholder="(1)" :style="{width:'217px'}"></el-input>
        </el-form-item>
        <el-form-item label="" :style="{display:'inline-block'}" prop="weight">
          <el-input v-model="ruleForm.weight" placeholder="(g)" :style="{width:'217px'}"></el-input>
        </el-form-item>
      </div>

      <!-- -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}" prop="size">
          <el-input v-model="ruleForm.size" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>
        <el-form-item label="" :style="{display:'inline-block'}" prop="material">
          <el-input v-model="ruleForm.material" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>
      </div>

      <!---->
      <el-form-item label="">
        <el-input type="textarea" v-model="ruleForm.introduction" placeholder=""
                  :style="{height:'100px'}"></el-input>
      </el-form-item>

      <!---->
      <p class="upload-txt"></p>
      <el-upload class="uploader-box" :show-file-list="false"
                 action="/museum-api/file/management"
                 name="uploadFile"
                 :headers=uploadHeader
                 :on-success="uploadCover">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="avatar-uploader-icon el-icon-plus"></i>
      </el-upload>

      <div class="line-title"></div>

      <!---->
      <el-form-item label="">
        <el-input v-model="ruleForm.application" placeholder=""></el-input>
      </el-form-item>

      <!-- -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.dynasty" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>

        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.age" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>
      </div>

      <!-- -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.craft" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>

        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.shape" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>
      </div>

      <!--  -->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.feature" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>

        <el-form-item label="" :style="{display:'inline-block'}">
          <el-input v-model="ruleForm.area" placeholder="" :style="{width:'217px'}"></el-input>
        </el-form-item>
      </div>

      <!-- ()-->
      <div>
        <el-form-item label="" :style="{display:'inline-block'}">
          <div class="r-star-box">
            <el-rate v-model="ruleForm.protectValue"></el-rate>
          </div>
        </el-form-item>
        <el-form-item label="" :style="{display:'inline-block'}" prop="cultureValue">
          <div class="r-star-box">
            <el-rate v-model="ruleForm.cultureValue"></el-rate>
          </div>
        </el-form-item>
      </div>

      <!-- -->
      <el-form-item label="">
        <el-input v-model="ruleForm.culturalConnotation" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="ruleForm.requiredTech" placeholder=""></el-input>
      </el-form-item>

      <!---->
      <p class="upload-txt"></p>
      <el-upload class="uploader-box" name="uploadFile" list-type="picture-card"
                 action="/museum-api/file/management"
                 :on-success="handleUploadImgs"
                 :data=uploadPrams
                 :on-preview="handlePictureCardPreview"
                 :limit=4 :on-exceed="overLimit"
                 :on-remove=handleRemove
                 :headers=uploadHeader>
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog size="tiny" :visible.sync=flag.previewVisiable>
        <img width="100%" :src="previewImg">
      </el-dialog>

      <!---->
      <p class="upload-txt"></p>

      <div class="uploader-box">
        <a href="javascript:;" class="r-file el-button--primary el-button--small el-button">
          <input type="file" @change="getFile($event)">
        </a>
        <div class="el-upload__tip">.f3d</div>
        <ul v-if="ruleForm.urn" class="el-upload-list el-upload-list--text"><!-- -->
          <li class="el-upload-list__item is-success">
            <a :href="$rootPath + 'edit/exhibit/view'" target="_blank" class="el-upload-list__item-name">
              <i class="el-icon-document"></i>{{ruleForm.urn ? ruleForm.urn : ''}}<span class="tip">()</span>
            </a>
            <label class="el-upload-list__item-status-label">
              <i class="el-icon-upload-success el-icon-circle-check"></i>
            </label>
            <i class="el-icon-close" @click="deleteModel(ruleForm.urn)"></i>
          </li>
        </ul>
      </div>

      <!-- -->
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  import _ from 'lodash'
  const ACCOUNT_ID = '564fIM6TfltXLD7AN1AQCOhT9V2GZBGe'
  const ACCOUNT_SECRECT = 'cepTLQSlLyDmz3i1'
  const BUCKET_KEY = 'mbucket1'

  export default {
    created(){
      // id
      this.$api.api_req('museum-api/relic/management/template', 'GET', {}, (_data) => { this.ruleForm.id = _data.data.id }, this.failure)
      // 
      this.$api.api_req('museum-api/relic-type/resources', 'GET', {}, (_data) => { this.relicType = _data.data }, this.failure)
      // token
      this.$api.api_forge('authentication/v1/authenticate', 'POST',
        {'Content-Type': 'application/x-www-form-urlencoded'}, // headers
        'client_id=' + ACCOUNT_ID + '&' + 'client_secret=' + ACCOUNT_SECRECT + '&' + 'grant_type=' + 'client_credentials' + '&' + 'scope=' + 'data:read data:write bucket:create bucket:read bucket:delete viewables:read',          // 
        this.getTokenSuc, this.failure
      )
    },
    data() {
      return {
        ruleForm: {
          // 
          id: 0,
          code: 0,
          // 
          relicType: '1',         // 
          name: '',              // 
          level: '',              // 
          attritionRate: '',    // 
          amount: '1',             // 
          weight: '-',             // 
          size: '',               // 
          material: '',           // 
          protectValue: 5,     // (
          cultureValue: 4,     // (
          picUrl: '',            // 
          // 
          application: '',       // 
          dynasty: '',            // 
          age: '',                 // 
          craft: '',              // 
          shape: '',              // 
          feature: '',          // 
          area: '',                   // 
          culturalConnotation: '',  // 
          requiredTech: '  ',          // 
          introduction: '',    // 
          urn: '',                      // 
          transformedUrn: '',
          bucketKey: 'mbucket1'
        },
        r_rules: {
          relicType: [
            {required: true, message: '', trigger: 'change'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 30, message: ' 1  30 ', trigger: 'blur'}
          ],
          level: [
            {required: true, message: '', trigger: 'change'}
          ],
          attritionRate: [
            {required: true, message: '', trigger: 'change'}
          ],
          amount: [
            {required: true, message: '', trigger: 'blur'}
          ],
          weight: [
            {required: true, message: '', trigger: 'blur'}
          ],
          size: [
            {required: true, message: '', trigger: 'blur'}
          ],
          material: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        relicType: [],
        imageUrl: '',                                   // element ui  
        tempImageList: [],                              // {id: 0, tempId: 0}   // idid tempId id
        uploadHeader: {
          'authorization': localStorage.getItem('token')// header token
        },
        uploadPrams: {
          imageIds: _.pick(this.tempImageList, ['id'])
        },
        flag: {
          previewVisiable: false
        },
        coverImgId: 0,
        previewImg: '',
        modelFile: ''
      }
    },
    methods: {
      // token
      getTokenSuc(data){
        localStorage.setItem('activeToken', data.data.access_token)
      },
      // 
      uploadCover(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw) // 
        if (this.coverImgId !== 0) {
          // cover
          this.$api.api_req('museum-api/relic/management/image', 'DELETE', {relicId: this.ruleForm.id, imageId: this.coverImgId}, () => {}, this.failure)
          this.coverImgId = 0
        }
        this.coverImgId = res.data.id
        this.$api.api_req('museum-api/relic/management/image', 'POST', {relicId: this.ruleForm.id, imageIds: [].concat(res.data.id)}, () => { this.$message({type: 'success', message: '!'}) }, this.failure) // 
        this.ruleForm.picUrl = res.data.url
      },
      // 
      handleUploadImgs(res, file){
        this.tempImageList.push({id: res.data.id, tempId: file.uid})
        this.$api.api_req('museum-api/relic/management/image', 'POST', {relicId: this.ruleForm.id, imageIds: _.map(this.tempImageList, 'id')}, () => { this.$message.success({message: ''}) }, this.failure)
      },
      // 
      handleRemove(_data){
        var delImgId = _.find(this.tempImageList, ['tempId', _data.uid]).id
        this.$api.api_req('museum-api/relic/management/image', 'DELETE', {relicId: this.ruleForm.id, imageId: delImgId}, () => {}, this.failure)
        this.tempImageList = _.reject(this.tempImageList, ['tempId', _data.uid])
      },
      // 
      getFile(event){
        this.modelFile = event.target.files[0]
        this.ruleForm.urn = event.target.files[0].name
        console.log(this.modelFile)
        this.submitFile()
      },
      // 
      submitFile(){
        this.$message.info('')
        console.log('')
        this.$api.api_forge('oss/v2/buckets/' + BUCKET_KEY + '/objects/' + this.ruleForm.urn, 'PUT',
          {'Content-Type': 'application/octet-stream', 'Authorization': 'Bearer ' + localStorage.getItem('activeToken')}, // headers
          this.modelFile,             // 
          this.submitFileSuc, this.failure)
      },
      submitFileSuc(data){
        this.$message.success('')
        console.log('')
        this.transformFile(data.data.objectId)
      },
      // 
      transformFile(urn){
        this.$api.api_forge('modelderivative/v2/designdata/job', 'POST',
          {'Content-Type': 'application/json', 'Authorization': 'Bearer ' + localStorage.getItem('activeToken')}, // headers
          {'input': {'urn': btoa(urn)}, 'output': {'formats': [{'type': 'svf', 'views': ['2d', '3d']}]}},          // 
          this.transformFileSuc,
          this.failure
        )
      },
      transformFileSuc(data){
        console.log(data.data)
        localStorage.setItem('transformedUrn', data.data.urn)
        this.ruleForm.transformedUrn = data.data.urn
        console.log('')
        this.$message.success(' ')
      },
      /*  */
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$api.api_req('museum-api/relic/management', 'PUT', this.ruleForm, this.submitFormSuc, this.failure)  // 
//            this.$api.api_req('museum-api/file/management', 'POST', this.ruleForm, this.submitFormSuc, this.failure) //
          } else {
            this.$message.error('')
            return false
          }
        })
      },
      submitFormSuc(_data){
        this.$message({type: 'success', message: '!'})
        this.$router.push({path: '/edit/exhibit/board'})
      },
      // 
      deleteModel(name){
        this.$api.api_forge('oss/v2/buckets/' + BUCKET_KEY + '/objects/' + name, 'DELETE',
          {'Authorization': 'Bearer ' + localStorage.getItem('activeToken')},
          {},
          this.delectObjectSuc, this.failure
        )
      },
      delectObjectSuc(data){
        console.log(data)
        this.modelFile = null
        this.ruleForm.urn = ''
        this.$message.success('')
      },
      handlePictureCardPreview(file){
        this.previewImg = file.url
        this.flag.previewVisiable = true
      },
      overLimit(){
        alert('4')
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      failure(_err){
        console.log('r_')
        console.log(_err)
      }
    },
    filters: {
      isNull(target){
        if (target) {
          return target
        } else {
          return ''
        }
      }
    }
  }
</script>

<style lang="stylus">
  .add-exhibit-wrapper
    width: 100%
    border: 1px solid #e6ebf5;
    background-color: #fff;
    color: #2d2f33;
    border-radius: 4px;
    .line-title
      color: #1f2f3d;
      font-size: 22px;
      font-weight: 400;
      border-left 5px solid #409eff
      margin: 20px 0 20px 0
      padding-left: 10px

  // 
  .r-star-box
    display inline-block
    vertical-align top
    padding-top 9px
    width: 207px;
    padding-left: 10px;

  .demo-ruleForm
    width: 640px

  // 
  .upload-txt
    margin: 20px
    margin-top: 5px

  .uploader-box
    border-radius: 3px;
    border: 1px solid rgb(235, 235, 235);
    padding: 24px;
    margin: 0 0 14px 14px;
    &:hover
      box-shadow: rgba(232, 237, 250, 0.6) 0px 0px 8px 0px, rgba(232, 237, 250, 0.5) 0px 2px 4px 0px;
    .el-upload
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    .el-upload:hover
      border-color: #409EFF;
    .avatar-uploader-icon
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;

  // 

  textarea
    height: 100%

  // 
  .r-file
    position: relative
    display: inline-block
    overflow: hidden
    text-decoration: none
    cursor pointer
    input
      position: absolute
      font-size: 100px
      right: 0
      top: 0
      opacity: 0
      cursor pointer
    &:hover
      background: #66b1ff
      border-color: #66b1ff
      color: #fff

  .tip
    padding-left: 20px
</style>



