<template>
  <div class="exhibitType-board-wrapper">
    <div class="r-oneline">
      <el-button @click="addNew" v-if="hasAuth(7)"></el-button>
    </div>

    <el-table border :data="userData">
      <el-table-column type='index' width='50'></el-table-column>
      <el-table-column property="account" label=""></el-table-column>
      <el-table-column property="name" label=""></el-table-column>
      <el-table-column property="password" label="" :formatter="fakePW"></el-table-column>
      <el-table-column property="id" label="id"></el-table-column>
      <el-table-column property="updateTime" :formatter="dateFormat" label="" width="200px"></el-table-column>
      <template v-if="hasAuth(9)||hasAuth(8)">
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)" type="text" size="small" v-show="hasAuth(9)"></el-button>
            <el-button @click="deleteItem(scope.row)" type="text" size="small" v-show="hasAuth(8)"></el-button>
          </template>
        </el-table-column>
      </template>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="flag.showEdit" class="r-box">
      <el-form :model="editForm" ref="editForm" :rules="rules">
        <el-form-item label="" prop="account" :label-width="formLabelWidth">
          <el-input v-model="editForm.account" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name" :label-width="formLabelWidth">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" :label-width="formLabelWidth">
          <el-input type="password" v-model="editForm.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass" :label-width="formLabelWidth">
          <el-input type="password" v-model="editForm.checkPass" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <!---->
      <el-checkbox :indeterminate="flag.isIndeterminate" v-model="flag.checkAll" @change="handleCheckAllChange"></el-checkbox>
      <el-checkbox-group class="r-radio-box" v-model="checkedRight" @change="handleCheckedCitiesChange">
        <el-checkbox v-for="item in rightName" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>

      <div slot="footer" class="dialog-footer">
        <el-button @click="flag.showEdit = false"> </el-button>
        <el-button type="primary" @click="editSubmit('editForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    created(){
      // 
      this.$api.api_req('museum-api/user/all-users', 'GET', {}, (_data) => { this.userData = _data.data }, this.failure)
      // 
      this.$api.api_req('museum-api/user/auth-menu', 'GET', {}, this.initAuthLists, this.failure)
      // 
      this.$api.api_req('museum-api/user/user-auth/id/' + localStorage.getItem('userId'), 'GET', {}, this.getCurrentAuth, this.failure, this.logicErr)
    },
    data(){
      // 
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else {
          if (this.editForm.checkPass !== '') {
            this.$refs.editForm.validateField('checkPass')
          }
          callback()
        }
      }
      // 
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.editForm.password) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }
      return {
        userData: [],
        authList: [],                       // 
        choicedAuthList: [],
        editForm: {
          id: '',
          account: '',
          name: '',
          password: '',
          checkPass: ''
        },
        flag: {
          showEdit: false,
          isIndeterminate: false,          // flag 
          checkAll: false
        },
        rules: {
          account: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 30, message: ' 1  30 ', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 30, message: ' 1  30 ', trigger: 'blur'}
          ],
          password: [
            {required: true, validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {required: true, validator: validatePass2, trigger: 'blur'}
          ]
        },
        rightName: [],                     //  
        rights: [],                        //  
        rightIndex: [],                    //  
        checkedRight: [],                  //  
        formLabelWidth: '80px'
      }
    },
    methods: {
      // 
      initAuthLists(data){
        data.data.menus.map((item, index) => {
          item.functions.map((item, index) => {
            this.rightName.push(item.functionName)      //  
            this.rights.push(item)                      //  
          })
        })
      },
      // 
      getCurrentAuth(_data){
        this.authList = []
        let tempData = Array.from(_data.data)
        tempData.shift()
        tempData.map(x => {
          this.authList.push(x.funcId)
        })
      },
      // 
      handleCheckAllChange(val) {
        this.checkedRight = val ? this.rightName : []   // true : false
        this.flag.isIndeterminate = false
      },
      // 
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length
        this.flag.checkAll = checkedCount === this.rightName.length
        this.flag.isIndeterminate = checkedCount > 0 && checkedCount < this.rightName.length
      },
      // 
      addNew(){
        this.$router.push({path: '/edit/manage/add'})
      },
      // 
      edit(row){
        // 
        this.flag = {showEdit: true, isIndeterminate: false, checkAll: false}
        this.editForm = {id: row.id, account: row.account, name: row.name, password: '', checkPass: ''}
        this.choicedAuthList = []
        this.checkedRight = []
        this.$api.api_req('museum-api/user/user-auth/id/' + row.id, 'GET', {}, this.getChoicedUserAuth, this.failure, this.logicErr)        // user
      },
      getChoicedUserAuth(_data){
        let tempData = Array.from(_data.data)
        console.log(tempData)
        tempData.map(x => {
          if (x.funcId === 1) { } else {
            this.choicedAuthList.push(x.funcId)
            this.checkedRight.push(_.find(this.rights, ['id', x.funcId]).functionName)
          }
        })
        this.flag.checkAll = this.choicedAuthList.length === this.rightName.length                                                // 
        this.flag.isIndeterminate = this.choicedAuthList.length > 0 && this.choicedAuthList.length < this.rightName.length        // 
      },
      editSubmit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.rightIndex = this.nameToIndex(this.checkedRight, this.rights, 'id')
            this.$api.api_req('museum-api/user/management', 'PUT',
              {account: this.editForm.account, password: btoa(this.editForm.password), name: this.editForm.password, id: this.editForm.id},
              () => {}, this.failure)
            this.$api.api_req('museum-api/user/user-auth', 'POST', {userId: this.editForm.id, authList: this.rightIndex}, this.editSubmitSuc, this.failure)
          } else {
            this.$message.error({message: ''})
            return false
          }
        })
      },
      editSubmitSuc(){
        this.$api.api_req('museum-api/user/all-users', 'GET', {}, (_data) => { this.userData = _data.data }, this.failure)
        this.flag.showEdit = false
        this.$message.success('')
      },
      deleteItem(row){
        this.$confirm('?', '', {confirmButtonText: '', cancelButtonText: '', type: 'warning'}).then(() => {
          this.$api.api_req('museum-api/user/management/id/' + row.id, 'DELETE', {}, this.deleteSuc, this.failure)
          this.$message({type: 'success', message: '!'})
        }).catch(() => { this.$message({type: 'info', message: ''}) })
      },
      deleteSuc(data){
        this.$api.api_req('museum-api/user/all-users', 'GET', {}, (_data) => { this.userData = _data.data }, this.failure)
        this.$message.success('')
      },
      /**
       *  
       * @param _nameList      (
       * @param model            (
       * @param target        id
       * @returns {Array}
       */
      nameToIndex(_nameList, model, target){
        let indexList = []
        _nameList.map((item, index) => {
          indexList.push(parseInt(_.find(model, ['functionName', item])[target]))
        })
        return indexList
      },
      // 
      fakePW(row, column){
        return '******'
      },
      hasAuth(index){
        return this.authList.indexOf(index) !== -1
      },
      // 
      dateFormat(row, column){
        return new Date(row[column.property]).toLocaleString()
      },
      failure(_err){
        console.log('')
        console.log(_err)
      },
      logicErr(_err){
        this.$message.error(_err)
      }
    }
  }
</script>

<style lang="stylus" scoped>
  // table 
  table
    tr
      form
        .el-form-item
          width: 50%
      td
        &:last-child
          border-right: 0

  // 
  .r-oneline
    margin-bottom: 20px
    .add-btn
      float left
    .search-form
      float right
      margin-bottom: 0
      .el-form-item
        margin-bottom 0
    &::after
      content: ''
      display: block
      clear: both

  .r-radio-box
    .el-checkbox
      width: 50%;
      margin: 0
</style>



