<template>
  <div class="friendship-board-wrapper">
    <div class="r-oneline">
      <el-button @click="flag.showAdd=true" v-if=hasAuth(10) class="add-btn"></el-button>
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item>
          <el-input v-model="searchForm.str" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-table border :data="linkList" style="width: 100%;">
      <el-table-column type='index' width='50'></el-table-column>
      <el-table-column property="name" label=""></el-table-column>
      <el-table-column property="id" label="id"></el-table-column>
      <el-table-column property="url" label="url"></el-table-column>
      <el-table-column property="image" label=""></el-table-column>
      <el-table-column property="updateTime" :formatter="dateFormat" label="" width="200px"></el-table-column>
      <template v-if="hasAuth(12)||hasAuth(11)">
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)" type="text" size="small" v-if="hasAuth(12)"></el-button>
            <el-button @click="deleteItem(scope.row)" type="text" size="small" v-if="hasAuth(11)"></el-button>
          </template>
        </el-table-column>
      </template>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="flag.showAdd">
      <el-form :model="addForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="url" :label-width="formLabelWidth">
          <el-input v-model="addForm.url" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.image" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="flag.showAdd = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="flag.showEdit">
      <el-form :model="editForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="url" :label-width="formLabelWidth">
          <el-input v-model="editForm.url" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.image" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="flag.showEdit = false"> </el-button>
        <el-button type="primary" @click="editSubmit"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    created(){
      // 
      this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1}, (_data) => { this.linkList = _data.data }, this.failure)
      // 
      this.$api.api_req('museum-api/user/user-auth/id/' + localStorage.getItem('userId'), 'GET', {}, this.getInitAuthList, this.failure, this.logicErr)
    },
    data(){
      return {
        linkList: [],
        authList: [],       // 
        flag: {
          showAdd: false,
          showEdit: false
        },
        addForm: {
          name: '',
          url: '',
          image: ''
        },
        editForm: {},
        searchForm: {
          str: ''
        },
        formLabelWidth: '80px'
      }
    },
    methods: {
      getInitAuthList(_data){
        this.authList = []
        _data.data.map(x => {
          this.authList.push(x.funcId)
        })
      },
      // 
      search(){
        if (/[0-9]/.test(this.searchForm.str)) {
          this.$message.info('id')
          this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1, id: this.searchForm.str}, (_data) => { this.linkList = _data.data }, this.failure)
        } else {
          this.$message.info('')
          this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1, name: this.searchForm.str}, (_data) => { this.linkList = _data.data }, this.failure)
        }
//        this.$message.info({type: 'info', message: ''})
      },
      addSubmit(){
        this.flag.showAdd = false
        this.$api.api_req('museum-api/friendship-link/management', 'POST', this.addForm, this.addSubmitSuc, this.failure)
      },
      addSubmitSuc(_data){
        this.$message.success('')
        this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1}, (_data) => { this.linkList = _data.data }, this.failure)
      },
      // ''
      edit(row){
        this.flag.showEdit = true
        this.$api.api_req('museum-api/friendship-link/resources', 'POST', {id: row.id, pageSize: 100, pageNum: 1}, this.searchEditItemSuc, this.failure)
      },
      searchEditItemSuc(_data){
        this.editForm = _data.data[0]
        console.log(_data.data[0])
      },
      // 
      editSubmit(){
        this.$api.api_req('museum-api/friendship-link/management', 'PUT', {id: this.editForm.id, name: this.editForm.name, url: this.editForm.url, image: this.editForm.image}, this.editSubmitSuc, this.failure)
      },
      editSubmitSuc(){
        this.flag.showEdit = false
        this.$message.success('')
        this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1}, (_data) => { this.linkList = _data.data }, this.failure)
      },
      // 
      deleteItem(row){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$api.api_req('museum-api/friendship-link/management/id/' + row.id, 'DELETE', {}, this.deleteSuc, this.failure)
        }).catch(() => {
          this.$message({type: 'info', message: ''})
        })
      },
      deleteSuc(_data){
        this.$api.api_req('museum-api/friendship-link/resources', 'POST', {pageSize: 100, pageNum: 1}, (_data) => { this.linkList = _data.data }, this.failure)
        this.$message({type: 'success', message: '!'})
      },
      hasAuth(index){
        return this.authList.indexOf(index) !== -1
      },
      // 
      dateFormat(row, column){
        return new Date(row[column.property]).toLocaleString()
      },
      failure(_err){
        console.log('')
        console.log(_err)
      },
      logicErr(_err){
        this.$message.error(_err)
      }
    }
  }
</script>

<style lang="stylus" scoped>
  // table 
  table
    tr
      form
        .el-form-item
          width: 50%
      td
        &:last-child
          border-right: 0

  // 
  .r-oneline
    margin-bottom: 20px
    .add-btn
      float left
    .search-form
      float right
      margin-bottom: 0
      .el-form-item
        margin-bottom 0
    &::after
      content: ''
      display: block
      clear: both
</style>



